.project {
    width: 250px;
    background-color: #e1e9f3;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.imgContainer {
    width: 100%;
    height: 200px;
    background-image: url("data:image/jpeg;base64,");
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.viewBlock {
    width: 60px;
    height: 40px;
    border: 1px solid;
    border-radius: 25px;
    background-color: #e1e9f3;
    opacity: 65%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.view {
    text-decoration: none;
    color: red;
    font-family:"Arial Black";
}